import React from "react";

class CheckboxWithLabel extends React.Component {

    static propTypes = {
        labelOff: React.PropTypes.string,
        labelOn: React.PropTypes.string
    };

    constructor(props) {
        super(props);
        this.state = {isChecked: false};

        // since auto-binding is disabled for React's class model
        // we can prebind methods herebab
        // http://facebook.github.io/react/blog/2015/01/27/react-v0.13.0-beta-1.html#autobinding
        this.onChange = this.onChange.bind(this);
    }

    onChange() {
        this.setState({isChecked: !this.state.isChecked});
    }

    render() {
        return <label>
                    <input checked={this.state.isChecked} onChange={this.onChange}
                        type="checkbox"
                    />
                        {this.state.isChecked ? this.props.labelOn : this.props.labelOff}
               </label>;
    }
}

module.exports = CheckboxWithLabel;
